import React, { Component } from 'react';
import { Button, Card } from 'react-bootstrap';

class Event extends Component {
  state = {
    collapsed: true
  }

  handleButtonClick = () => {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }

  render() {
    const { event } = this.props;
    const { collapsed } = this.state;

    return (
      <Card body className="event">
        <Card.Title className="title" as="h5">{event.summary}</Card.Title>
        <Card.Text className="event-body">
          { event.start && event.start.dateTime && event.start.timeZone && (
            <p className="date">
              {event.start.dateTime} ({event.start.timeZone})
            </p>
          )}
          <p className="location">{event.location}</p>

          { !collapsed && (
            <div className="event-details">
              <a href={event.htmlLink} target="_blank" rel="noreferrer" className="link">
                See details on Google Calendar
              </a>
              <p className="description">{event.description}</p>
            </div>
          )}
          <Button className="toggle-details shadow-none" onClick={this.handleButtonClick}>
            { collapsed ? "Show Details" : "Hide Details" }
          </Button>
        </Card.Text>
      </Card>
    );
  }
}

export default Event;
