import React, { Component } from 'react';
import { Form, ListGroup } from 'react-bootstrap';

class CitySearch extends Component {
  state = {
    query: '',
    suggestions: [],
    showSuggestions: undefined
  }

  handleInputChanged = (e) => {
    const value = e.target.value;
    const suggestions = this.props.locations.filter((location) => {
      return location.toUpperCase().indexOf(value.toUpperCase()) > -1;
    });

    this.setState({
      query: value,
      suggestions
    });
  }

  handleItemClicked = (suggestion) => {
    this.setState({
      query: suggestion,
      showSuggestions: false
    });

    this.props.updateEvents(suggestion);
  }

  render() {
    return (
      <Form className="CitySearch mb-3">
        <Form.Group className="form" controlId="formCity">
          <Form.Label className="form-label mb-1">Select a City:</Form.Label>
          <Form.Control className="form-input shadow-none city"
            type="text"
            value={this.state.query}
            onChange={this.handleInputChanged}
            onFocus={() => this.setState({ showSuggestions: true })}
          />
          <ListGroup className="suggestions" style={this.state.showSuggestions ? {} : { display: 'none' }}>
            { this.state.suggestions.map((suggestion) => (
              <ListGroup.Item
                action
                as="li"
                key={suggestion}
                onClick={() => this.handleItemClicked(suggestion)}
              >{suggestion}</ListGroup.Item>
            ))}
            <ListGroup.Item action onClick={() => this.handleItemClicked('all')}>
              See all cities
            </ListGroup.Item>
          </ListGroup>
        </Form.Group>
      </Form>
    );
  }
}

export default CitySearch;
