import React, { Component } from 'react';
import { Form } from 'react-bootstrap';

class NumberOfEvents extends Component {
  state = {
    numberOfEvents: 32
  }

  handleInputChanged = (e) => {
    const value = e.target.value;
    if (value < 1 || value > 50) {
      this.setState({
        numberOfEvents: e.target.value,
        infoText: 'Please enter a number between 1 and 50.'
      });
    } else {
      this.setState({
        numberOfEvents: value,
        infoText: ''
      });

      this.props.updateNumberOfEvents(value);
    }
  }

  render() {
    return (
      <Form className="NumberOfEvents mb-3">
        <Form.Group className="form" controlId="formNumberOfEvents">
          <Form.Label className="form-label mb-2">Number of Events:</Form.Label>
          <Form.Control
            className="form-input shadow-none number-of-events"
            type="number"
            value={this.state.numberOfEvents}
            onChange={this.handleInputChanged}
            placeholder="Enter a number between 1 and 50"
          />
          <div className="error-alert" text={this.state.infoText}>
            {this.state.infoText}
          </div>
        </Form.Group>
      </Form>
    );
  }
}

export default NumberOfEvents;
